    "text": `<p>Hi, my names Angel. Welcome to my talk. How is everyone doing? More importantly how are you doing as an individual right now? I want to talk to you personally. Can we take a moment?</p><p>Are you there?</p><p>Great, I hope your here to take this journey with me. Perhaps, it is just me alone on this journey. Which is ok. If that is the case, I feel I am in good company. Let me take this moment to tell you about yourself. </p><p>Or about myself, I think that is where this is going. And since we are talking about myself good news, I found my pants. Thats an inside joke with myself (see what I did there). Youre not a true myselfer if you don't get it. Sorry bro its a my-self thing. </p><p>Language is funny. A dull and insufficient instrument at times. Other times it can cut razor sharp. However, I'm not sure if theres any instrument sharp enough to capture the clarity of the image I want to portray. Perhaps polished be it may. </p><p>Polished enough to see oneself. </p><p>Thanks for coming to my talk. </p><p>Wait that doesn't sound right. Okay, so look we have a lot of protocols for dealing with one another. Especially now, ya know, with everything thats going on. So hear me out. I got this shiny new invention thats going to completely solve all this. </p><p>Its a program Ima call "Quote" that makes a record of what you last said, complete sentence or maybe by past seconds. Kinda like that program that handles log entries on Star Trek. That would make writing this a lot easier. Except I'm not saying any of this out loud. Would be better if it was written. This the same reason I don't wanna talk to you people about getting work done over the phone. I rather have our conversation in messages so we can look back on them and shit. I don't know. But also, just to help people get ones thoughts together and their mind right. Recharging your workplace culture, all that. Bro this is going bad.</p><p>Check me out though, its not like you don't already have a ton of programs listening to your audio now anyways. With this you know we listenen to you at all times. And we will make a log of other applications that are recording you and let you know they recording too. Ay and look, you can keep all your recordings to yourself and shit ion even care. Its going to be like your own privacy fence, starting with audio. And its gonna give you a quote of all your data thats being used by other companies too. </p><p>You know what sucks. Finding out something you were in support of might not of been in your best interest. Now you gotta cast it aside like it disappointed you the whole time type shit. Or nah maybe not the whole time. But large sections of it. Time</p><p>Wow, this is starting to feel like a waste of it, am I right? </p><p>Cause I feel like I'm wrong. </p><p>This is atleast definately a waste of space. Can we agree? </p><p>Why are you so tense. Feel like I'm trying to sell you something. If I could just get you to pullout your phone and download this app. </p><p>Nah but this is serious talk bro. Thats why I'm glad I could share it. With you. And thank you for being such good company. </p>`,
